
/* Structure of all the text*/

.container {
  
  margin-top: 100px;
  margin-left: 150px;
  margin-right: 150px;


}

#Introduction {padding-top:30px;}
#American_Eco {padding-top:30px;}
#Introduction {padding-top:30px;}
#World{padding-top:30px;}
#Conclusion{padding-top:30px;}

/* The hero image */
.hero-image {

  /* Set a specific height */
  height: 80%;

  /* Position and center the image to scale nicely on all screens */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  width: 100%;
  top: -10px;
}

/* Place text in the middle of the image */
.hero-text {
  font-family: Georgia, serif;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ffffff;
  text-shadow: 3px 3px rgb(119, 119, 119);
  font-size: 50px;
  
}

footer{
  display: inline-block;
  text-align: center;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  top: 0px;
  
  margin-left: 0%;
  background-color: rgb(255, 255, 255);
  
}
//center the html plots
.centerPlot{
  align-items: center;
  justify-content: center;
}
html{
  scroll-behavior: smooth;
  height: 100%;
  width: 100%;
  margin-left: 0%;
}


/** page layout **/
main {
  display: grid;
  //grid-template-columns: 1fr 15em;
  max-width: 100em;
  width: 100%;
  margin: 0 auto;
}
/**center the images**/
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

/**color for the links**/
a:link, a:visited { 
  color: lightskyblue;
  //text-decoration: underline;
  cursor: auto;
}

/* Change the font of all the page */
a,p, h1,h2,h3,h4,h5,h6 {
  font-family: 'Nunito', sans-serif;
  line-height: 150%;
  text-align: justify;
}
a,p{
  font-size: 20px;
}


.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.5s;
  //width: 25%;
  //height: 300px; to see at same height for a moment
  //display: table-cell;
  //margin: 1px;
  text-align: center;
  //float: left;
  border-radius: 8px;
  height: max-content;

}
/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.4);
}

.title {
  color: grey;
  font-size: 18px;
  margin: 40px;

}

a {
  text-decoration: none;
  font-size: 20px;
  color: black;

}

button:hover, a:hover {
  opacity: 0.7;
}

/* Float four columns side by side */
.column {
  float: left;
  width: 25%;
  padding: 0 0px;

}

/* Remove extra left and right margins, due to padding in columns */
.row {margin: 0 -5px;}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Responsive columns - one column layout (vertical) on small screens */
@media screen and (max-width: 900px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}
